import React, { Component } from "react";
import Slider from "react-slick";
import './Slider.css'

export default class PreviousNextMethods extends Component {
    state={
        wordsArray:[
            {autor:'Jim McCarthy',
            img:'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRTuCJZtzavcEEto4E5nu0Iyve1Sm13Nve8mjlcQZUer_OabUVO',
        desc:'You cannot create good programs without a good team, but most software teams behave like a problem family.',},
        {autor:'Bill Gates',
            img:'https://fm.cnbc.com/applications/cnbc.com/resources/img/editorial/2018/07/11/105322791-1531301768595gettyimages-467620670.1910x1000.jpg',
        desc:'Measuring the productivity of programming by counting lines of code is the same as evaluating the construction of an aircraft by its weight.'},
        {autor:'Seymour Cray',
            img:'https://www.google.com/search?q=Seymour+Cray&rlz=1C1GCEA_enUA832UA832&source=lnms&tbm=isch&sa=X&ved=0ahUKEwivg-uk8JfgAhXP-yoKHY6AANMQ_AUIDigB&biw=1517&bih=730#imgdii=Mbbbfqwrw5SEdM:&imgrc=-pZOkPfRQuAANM:',
        desc:'The difficulty of working with a programmer is that you cannot understand what he is doing until it is too late.',},
        ]    
    }
  constructor(props) {
    super(props);
    this.next = this.next.bind(this);
    this.previous = this.previous.bind(this);
  }
  next() {
    this.slider.slickNext();
  }
  previous() {
    this.slider.slickPrev();
  }
  render() {
    const settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      width:30
    };
    return (
      <div className='general_slider' >
        <h2 className='qutes_human'>Quotes of great men</h2>
        <Slider ref={c => (this.slider = c)} {...settings}>
          <div   key={1}>
          <div className='div_outline'>
          <p className='slider_desc'>'You cannot create good programs without a good team, but most software teams behave like a problem family.'</p>
           <div className='div_commponent'><img className="component_image" src='https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRTuCJZtzavcEEto4E5nu0Iyve1Sm13Nve8mjlcQZUer_OabUVO' alt=""/>
            <h6 className='author_name'>Jim McCarthy</h6></div> 
          </div>
          </div>
          <div  key={2}>
          <p className='slider_desc'>Measuring the productivity of programming by counting lines of code is the same as evaluating the construction of an aircraft by its weight.'</p>
           <div className='div_commponent'><img className="component_image" src='https://fm.cnbc.com/applications/cnbc.com/resources/img/editorial/2018/07/11/105322791-1531301768595gettyimages-467620670.1910x1000.jpg' alt=""/>
            <h6 className='author_name'>Bill Gates</h6></div> 
          </div>
          <div  key={3}>
          <p className='slider_desc'>'The difficulty of working with a programmer is that you cannot understand what he is doing until it is too late.'</p>
           <div className='div_commponent'><img className="component_image" src='https://sc18.supercomputing.org/app/uploads/2018/03/seymour-cray.jpg' alt=""/>
            <h6 className='author_name'>Seymour Cray</h6></div> 
          </div>
        </Slider>
        <div style={{     textAlign: 'right',
    position: 'relative',
    top: '-14%' }}>
          <button className="bton" onClick={this.previous}>
          &lt;	
          </button>
          <button className="bton" onClick={this.next}>
          &gt;
          </button>
        </div>
      </div>
    );
  }
}