import React, { Component } from 'react'
import './Home.css';
import { Carousel } from "react-responsive-carousel";
class Home extends Component {
      state={
            class:['Frontend-developer(React) ','Javascript-developer '],
            index:0,
            res:'',
            final:'Hello on My Page'
          }
        
          changeString=()=>{
            if(this.state.index==0){
               this.setState(prev=>({
                final:this.state.class[0],
                index:1
              }))}
              else if (this.state.index==1){
                this.setState(prev=>({
                  final:this.state.class[1],
                  index:0
                }))
              }
            console.log(this.state.final);
        }
        redurWait=()=>{
          let timer=setTimeout(this.changeString,3000)
        };
       
  render() {
    this.redurWait()
    return (
        <div className='home_gen_div'> <div>
            <h1 className='home-main_title'>Yurii Oleshko</h1>
            </div>
            <h3 className={`home-main_suptitle onet`}>{this.state.final}</h3>   
            <div className='main-list'>
            <ul className='home_list'> 
                <li className='home_list-item'> <span className="list_item_title"> Age</span>  <span className='list_item_value'>  23</span></li>
            <li className='home_list-item'> <span className="list_item_title"> Address </span> <span className='list_item_value'>  Ukraine, Kiev</span></li>
            <li className='home_list-item'> <span className="list_item_title"> e-mail</span>   <span className='list_item_value'> yurii.oleshko@gmail.com</span></li>
            <li className='home_list-item'> <span className="list_item_title"> Phone </span>  <span className='list_item_value'> +38067634463</span></li>
            <li className='home_list-item'> <span className="list_item_title"> skype </span>  <span className='list_item_value'> urry_longer</span></li></ul>
            </div>
        </div>
      
    )
  }
}
export default  Home